@import url(https://fonts.googleapis.com/css?family=Lato|Merriweather|Open+Sans|Open+Sans+Condensed:300|Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  text-align: center;
}

.app-header {
  background-color: #282c34;
  min-height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.greeting {
  width: 100%;
}

.student-input-container {
  display: flex;
  justify-content: center;
}

input {
  height: 40px;
  width: 20vw;
  margin: 20px;
  padding: 5px;
  letter-spacing: 1px;
  font-family: 'Lato', sans-serif;
}

input:focus {
  outline: 0;
}

input.grade-input {
  width: 4vw;
}

button {
  width: 125px;
  height: 50px;
  margin-top: 20px;
  box-shadow: none;
  border-radius: 5px;
  font-family: 'Lato', sans-serif;
}

button:hover {
  cursor: pointer;
  background-color: #282c34;
  color: white;
}

button:disabled {
  cursor: not-allowed;
}

.exam-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid lightgray;
  margin-bottom: 15px;
}

.entry-container {
  display: flex;
  justify-content: center;
}

.entry-container.failing {
  background-color: rgba(200, 0, 0, 0.8);
}

p {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 20vw;
  padding: 5px;
  letter-spacing: 1px;
  font-family: 'Lato', sans-serif;
}

.delete-link:hover {
  cursor: pointer;
}

